/* color: #F03799 pink */
.aboutme{
    /* background-image: linear-gradient(180deg,#FFFFFF,#651C8C); */
    min-height: 100vh;
    display:flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding:20px 50px;
}
.heading h1{
    font-size: xx-large;
    font-weight: 700;
    margin-bottom: 45px;
}
.description{
    display: flex;
    flex-direction: row;
}
.description .desc1{
    max-width: 50%;
    box-shadow: 2px 2px 18px black;
    padding: 25px 45px 5px;
    border-radius: 15px;
}
.desc1 p{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: large;
    font-weight: 500;
    padding-top: 10px;
}
.desc1 span{
    font-size: x-large;
    font-weight: bold;
}
.description .desc2{
    min-width: 50%;
    box-shadow: 2px 2px 18px black;
    border-radius: 15px;
    margin: 0 10px;
    padding:15px 80px 5px;
    height:100vh;
}
.skill-heading{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.desc2 h1{
    
    font-size: x-large;
    font-weight: 700;
    margin-bottom: 20px ;
}
.skills p{
    padding: 5px;
    font-weight: 600;
    
}
.container{
    background-color: white;
    border-radius: 8px;
    width: 100%;
    height: 30px;
    box-shadow: 1px 1px 18px black;

}
.skillset{
    border-radius: 8px;
    border: 2px solid blueviolet;
    text-align: end;
    color: white;
    height: 31px;
    background-color: blueviolet;
    

}
.HTML{
    width:95%;
    
}
.CSS{
    width:90%;
}
.js{
    width:87%;
}
.react{
    width:77%;
    
}
.wordpress{
    width:67%
}
